<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <i class="el-icon-location-outline"></i> 
        </el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="borderBox">
        <div class="flex headSelect" style="margin-bottom:10px;">
          <div class="flex">
            <img class="titleIcon" src="../../../assets/img/form.png" alt />
            <div style="margin-top: -4px;    width: 74px;"></div>
          </div>

          <div class="flex funcHead">
            <div class="block">
              <span class="demonstration" style="margin-left:10px" ></span>
              <el-input style="width:140px" v-model="kName" placeholder=""></el-input>
            </div>
            <div class="block">
              <span class="demonstration" style="margin-left:10px" ></span>
              <el-select style="width:140PX" v-model="jName" placeholder="">
                <el-option
                  v-for="item in options"
                  :key="item.roleId"
                  :label="item.name"
                  :value="item.name">
                </el-option>
              </el-select>
            </div>
            <el-button
              style="margin-left:10px"
              @click="getData"
              type="primary"
              icon="el-icon-search"
            ></el-button>
            <el-button
              style="margin-left:10px"
              @click="exportCode()"
              type="primary"
              icon="el-icon-download"
            ></el-button>
            <el-button
              style="margin-left:10px"
              @click="exportDemo()"
              type="primary"
              icon="el-icon-download"
            ></el-button>
            <el-button
              style="margin-left:10px"
              @click="upload()"
              type="primary"
              icon="el-icon-upload2"
            ></el-button>
            <el-button
              style="margin-left:10px"
              @click="openAdd(1)"
              type="primary"
              icon="el-icon-plus"
            ></el-button>
          </div>
        </div>
        <el-table
            :data="tableData"
            style="width: 100%"
            v-loading="loading"
          >
          <el-table-column type="index" label=""  width="55"></el-table-column>
          <el-table-column prop="loginname"  label=""  ></el-table-column>
          <el-table-column prop="username"  label=""  ></el-table-column>
          <el-table-column prop="roleName"  label=""  ></el-table-column>
          <el-table-column prop="positionName"  label=""  ></el-table-column>
          <el-table-column prop="departmentName"  label=""  ></el-table-column>
          <el-table-column prop="code"  label=""   >
                <template slot-scope="scope">
                        <span class="pointer"  @click="qrCode(scope.$index, scope.row)"></span>
                 </template>
          </el-table-column>
          <!-- <el-table-column prop="opAt"  label=""   >
          </el-table-column> -->
          <el-table-column prop="time" :formatter="timeFormate"  label="" width="140"  ></el-table-column>
          <el-table-column  label="" width="150px"  > 
                <template slot-scope="scope">
                        <span class="pointer"  @click="detials(scope.$index, scope.row)"  ></span>
                        <span class="pointer" style="margin-left:10px"   @click="deal(scope.$index, scope.row)"></span>
                        <!-- <span class="pointer" style="margin-left:10px"  @click="reset(scope.$index, scope.row)"></span> -->
                 </template>
          </el-table-column>
        </el-table>
        <div class="pagination">
          <el-pagination
            background
            @current-change="handleCurrentChange"
            layout="total, prev, pager, next, jumper"
            :total="total"
          ></el-pagination>
        </div>
      </div>
    </div>

    <!--  -->
    <el-dialog :title="mTitle" :visible.sync="editVisible" width="40%"  >
        <el-form ref="ruleForm"  :model="ruleForm"  :rules="rules" label-width="120px" v-loading="loading1"  >
            <el-form-item label="" prop="loginname" required>
                  <el-input v-model="ruleForm.loginname" ></el-input>
            </el-form-item>
            <el-form-item label="" prop="password" required>
                  <el-input v-model="ruleForm.password" ></el-input>
            </el-form-item>
            <el-form-item label="" prop="username" required>
                  <el-input v-model="ruleForm.username" ></el-input>
            </el-form-item>
            <el-form-item label="" prop="jobNumber" required>
                  <el-input v-model="ruleForm.jobNumber" ></el-input>
            </el-form-item>
            <el-form-item label="" prop="roleId" required>
                   <el-select style="width:100%" v-model="ruleForm.roleId" placeholder="">
                     <el-option
                      v-for="item in options"
                      :key="item.roleId"
                      :label="item.name"
                       :value="item.roleId"
                      >
                    </el-option>
                  </el-select>
            </el-form-item>
            <el-form-item label="" prop="departmentId" required>
                   <el-select style="width:100%" v-model="ruleForm.departmentId" placeholder="">
                     <el-option
                      v-for="item in departments"
                      :key="item.id"
                      :label="item.name"
                       :value="item.id"
                      >
                    </el-option>
                  </el-select>
            </el-form-item>
             <el-form-item label="" prop="permission">
                     <el-radio v-model="ruleForm.permission" label=""></el-radio>
                     <el-radio v-model="ruleForm.permission" label=""></el-radio>
                     <el-radio v-model="ruleForm.permission" label=""></el-radio>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer"  >
            <el-button @click="editVisible = false"> </el-button>
            <el-button type="primary" @click="saveEdit('ruleForm')"> </el-button>
        </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="delVisible" width="300px" center>
        <div class="del-dialog-cnt"></div>
        <span slot="footer" class="dialog-footer">
            <el-button @click="delVisible = false"> </el-button>
            <el-button type="primary" @click="deleteRow"> </el-button>
        </span>
    </el-dialog>
   
   <!--  -->
    <el-dialog title="" :visible.sync="visible" width="40%"  >
        <el-form ref="ruleForm"   label-width="120px" v-loading="loading1"  >
            <el-form-item label=""  >
                  <el-upload
                    ref="upload"
                    class="upload-demo"
                    accept=".xls"
                    :limit="1"
                    :auto-upload="false"
                    action="https://jsonplaceholder.typicode.com/posts/"
                   >
                    <el-button size="small" type="primary"></el-button>
                  </el-upload>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer"  >
            <el-button @click="visible = false"> </el-button>
            <el-button type="primary" @click="uploads"> </el-button>
        </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="codeVisible" width="40%"  >
        <div class="flex" style="height:120px;background:#FEFF00;">
                <div style="margin-top: 2px;" ><v-Code :name="codeText" ></v-Code></div>
                <div style=" color: #000;padding-left:20px">
                    <div style="margin: 5px 0px 18px; font-size: 18px;font-weight: 600;">{{codeText.name}}</div>
                    <div></div>
                    <div>yiliaofeiwuzhuisu</div>
                </div>
        </div>
        <span slot="footer" class="dialog-footer"  >
            <el-button type="primary"  @click="codeVisible = false"> </el-button>
        </span>
    </el-dialog>

  </div>
</template>

<script>
const moment = require("moment");
import vCode from '../../common/qrcode.vue';
export default {
  data() {
    return {
      loading: false,
      loading1: false,
      delVisible: false,
      visible: false,
      codeVisible: false,
      codeText:'',
      kName:'',
      jName:'',
      mTitle:'',
      tableData: [
        {
          id: 1,
          name: "",
          user:'qw12',
          time: "2018-10-08 14:44",
          name1:'121212',
          status:true,
          power:''
        }
      ],
      total: 0,
      cur_page: 1,
      editVisible:false,
      ruleForm:{
        loginname:'',
        password:'',
        username:'',
        jobNumber:'',
        departmentId:'',
        roleId:'',
        permission:'',
      },
      rules: {
          loginname: [
              { required: true, message: '' }
          ],
          password: [
              { required: true, message: '' }
          ],
          username: [
              { required: true, message: '' }
          ],
          jobNumber: [
              { required: true, message: '' }
          ],
          departmentId: [
              { required: true, message: '' }
          ],
          roleId: [
              { required: true, message: '' }
          ],
      },
      options:[
      ],
      departments:[
      ]
    };
  },
  components:{
         vCode
  },
  methods: {
    timeFormate(row){
         return moment(row.opAt).format('YYYY-MM-DD HH:mm:ss');
    },
    // 
    handleCurrentChange(val) {
      this.cur_page = val;
      // this.getTask();
    },
    // 
    search() {
      console.log(this.date);
    },
    openAdd(type,row){
      this.loading1=false;
      if(type==1){
        this.mTitle='';
        this.ruleForm={
              loginname:'',
              password:'',
              username:'',
              jobNumber:'',
              departmentId:'',
              roleId:'',
              permission:'',
        };
        if(this.$refs['ruleForm']){
           this.$refs['ruleForm'].resetFields();
        }
      }else{
        this.ruleForm.password='';
        if(this.$refs['ruleForm']){
           this.$refs['ruleForm'].resetFields();
        }
        this.mTitle='';
        var form=this.ruleForm;
        for(var key in row){
          form[key]=row[key]
        }
        this.ruleForm=form;
      }
      this.editVisible=true;
    },
    detials(index,row){
      this.openAdd(2,row);
    },
    // 
    saveEdit(formName) {
        this.$refs[formName].validate((valid) => {
            if (valid) {
              if(this.mTitle==''){
                this.add()
              }else{
                this.edit()
              }
            } else {
                return false;
            }
        });
    },
    // 
    add(){
        this.loading1=true;
        this.$axios({
              method:'post',
              url:'/platform/sys/user/addDo',
              data:this.ruleForm,
          }).then((res) =>{
              if(res.data.code==0){
              this.loading1=false;
              this.editVisible = false;
              this.getData()
              }else{
                  this.$message.error(res.data.msg);
              }
          }).catch((error) =>{
              console.log(error)    
          })
    },
     // 
    edit(){
        this.loading1=true;
        this.$axios({
              method:'put',
              url:'/platform/sys/user/editDo',
              data:this.ruleForm
          }).then((res) =>{
              if(res.data.code==0){
              this.loading1=false;
              this.editVisible = false;
              this.getData()
              }else{
                  this.$message.error(res.data.msg);
              }
          }).catch((error) =>{
              console.log(error)    
        })
    },
    // 
    deleteRow(){
        this.$axios({
              method:'DELETE',
              url:'/platform/sys/user/delete?ids='+this.id,
          }).then((res) =>{
              if(res.status==200){
                  this.$message.success('');
                  this.delVisible=false;
                  this.getData();
              }else{
                  this.$message.error(res.data.msg);
              }
          }).catch((error) =>{
              console.log(error)    
        })
    },
    deal(index,row){
      this.id=row.id;
      this.delVisible=true;
    },
    exportCode(){
      console.log(11)
    },
    upload(){
      this.visible=true
    },
    uploads(){
      this.$refs.upload.submit();
    },
    qrCode(index,row){
        this.codeText={id:row.id,name:row.username}
        this.codeVisible=true;
    },
    statusChange(){
        
    },
    // 
    reset(){
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {         
        });
    },
    getJob(){
        this.$axios({
            method:'get',
            url:'/platform/hospital/position/data',
        }).then((res) =>{
            if(res.status==200){
                this.options=res.data.data;
            }else{
                this.$message.error(res.data.msg);
            }
        }).catch((error) =>{
            console.log(error)    
        })
        this.$axios({
            method:'get',
            url:'/platform/hospital/department/data',
        }).then((res) =>{
            if(res.status==200){
                this.departments=res.data.data;
            }else{
                this.$message.error(res.data.msg);
            }
        }).catch((error) =>{
            console.log(error)    
        })
    },
    getData(){
        this.loading=true;
        this.$axios({
            method:'get',
            url:'/platform/sys/user/listPage??pageNumber='+this.cur_page+'&pageSize=10&username='+this.kName+'&positionName='+this.jName,
        }).then((res) =>{
            if(res.status==200){
                this.loading=false;
                this.tableData=res.data.list.map((ele)=>{
                  ele.permission=ele.permission?ele.permission:'';
                  return ele
                });
                this.total=res.data.totalCount;
            }else{
                this.$message.error(res.data.msg);
            }
        }).catch((error) =>{
            console.log(error)    
        })
    },
  },
  mounted() {
    this.getJob()
    this.getData()
  }
};
</script>


<style scoped>
.cloudBox > div {
  text-align: center;
  box-sizing: border-box;
  overflow: hidden;
  min-height: 300px;
  position: relative;
}

.upload-demo /deep/ .el-upload--text{
  height:40px;
  width:100%;
}

</style>




